<template>
    <div>
        <h1 class="divided">VIP</h1>
        <go-back />
         <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading && !disabled">
            <div>
                <h3>VVIP</h3>
                <table class="labryinth-table">
                    <thead>
                        <th>VVIP等级</th>
                        <th>需要排名</th>
                        <th>会员福利</th>
                    </thead>
                        <tr v-for="(v,k) in result.vvip" :key="k">
                            <td>
                            <div class="flex-vip">
                                <img :src="imgurl(v._FileName)">
                                {{v.name}}
                            </div>
                            </td>
                            <td v-if="v._VvipRankMin == v._VvipRankMax">全区消费排名第{{v._VvipRankMin}}名</td>
                            <td v-else>全区消费排名第{{v._VvipRankMin}} ~ {{v._VvipRankMax}}名</td>
                            <td>
                                <div class="flex-vip">
                                    <div>
                                        <ul v-for="(v1,k1) in v.reward" :key="k1" class="fa-ul">
                                        <li><span class="fa-li"><i class="fa fa-check" style="color:#42d642;"></i></span>{{v1}}</li>
                                        </ul>
                                    </div>
                                    <div>
                                        <item-icon-tooltip
                                            :itemId="v.item.id" 
                                            :itemStub="v.item"
                                            :count="1"
                                            :noLink="noLink">
                                        </item-icon-tooltip>
                                    </div>
                                </div>
                            </td>
                        </tr>
                </table>
            </div>
            <div>
                <h3>VIP</h3>
                <table class="labryinth-table">
                    <thead>
                        <th>VIP等级</th>
                        <th>需要点数</th>
                        <th>会员福利</th>
                    </thead>
                        <tr v-for="(v,k) in result.vip" :key="k">
                            <td>
                                <div class="flex-vip">
                                <img :src="imgurl(v._FileName)">
                                {{v.name}}
                                </div>
                            </td>
                            <td>{{v._VipPointMin}} ~ {{v._VipPointMax}}</td>
                            <td><ul v-for="(v1,k1) in v.reward" :key="k1" class="fa-ul">
                                <li><span class="fa-li"><i class="fa fa-check" style="color:#42d642;"></i></span>{{v1}}</li>
                                </ul>
                            </td>
                        </tr>
                </table>
            </div>
            <h3>VIP会员商店</h3>
            <shop-browser :shopId="shopId"  :useClick="useClick" />
            <back-to-top class="topButton"><i class="fa fa-arrow-up"></i></back-to-top>
        </template>
        <template v-if="disabled">当前区域无法查看该图鉴</template>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import Loader from "@/components/util/Loader";
import ShopBrowser from '@/components/item/shop/ShopBrowser.vue';
import BackToTop from '@/components/util/BacktoTop';
Vue.component("load-indicator", Loader);
export default {
    created(){
        if(this.$store.state.regionCode === 'cn'){
            this.fetchData();
        }else{
            this.loading = false;
            this.disabled = true;
        }
    },
    components: {
        GoBack,
        ItemIconTooltip,
        ShopBrowser,
        BackToTop
    },
    data(){
        return{
            result:null,
            noLink:false,
            loading:true,
            shopId:220001,
            useClick:true,
            disabled:false,
        }
    },
    methods:{
        fetchData(){
            if(GameInfo.vipcache){
                this.result = GameInfo.vipcache;
                this.loading = false;
            }else{
            GameInfo.getVip(this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        },
        imgurl(file){
            return `https://reference.dn.divinitor.com/api/server/ko/dds/${file}/png`;
        }
    }
}
</script>
<style lang="less" scoped>
.labryinth-table{
    margin:0 auto;
}

.flex-vip{
    display:flex;
    align-items:center;
    justify-content:space-around;
}
</style>