<template>
<div class="first-run" v-if="visible">
    <div class="modal-shade" />
    <div class="modal">
        <div class="modal-inner" 
            v-anime="{
                translateY: ['-100px', 0],
                opacity: {
                    value: ['0.0', '1.0'],
                    duration: 5000,
                },
                delay: 500,
                duration: 750,
                elasticity: 100,
            }">
            <div class="modal-content" :style="`height:${this.height};`">
                
                <div class="page" :active="true" :id="0">
                    <div class="content content-in" :active="true">
                        <h3>{{title}}</h3>
                        <slot />
                        <div class="got-it">
                            <button class="dv-button" v-on:click="finish"
                                v-anime="{
                                    borderColor: ['#98BCD4', '#dcf1ff', '#98BCD4', '#98BCD4'],
                                    loop: true,
                                    duration: 3000,
                                    delay: 750,
                                    easing: 'easeInOutExpo'
                                }">
                                确定
                            </button>
                        </div>
                </div>
            </div>
        </div>
    </div>
    </div>
</div>
</template>

<script>
export default({
    name:"dialog-frame",
    props: {
        "visible":{
            type: Boolean,
            default: false,
        },
        "title": {
            type: String,
            default: '',
        },
        "height": {
            type: String,
            default: '250px',
        },
    },
    methods:{
        finish() {
            this.$anime({
                targets: ".first-run",
                opacity: [1.0, 0.0],
                duration: 1000,
                easing: "easeOutCubic",
            })

            this.$emit('closePopup',false);
        }
    }

});
</script>

<style lang="less" scoped>
@import "../../less/core.less";

.first-run {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 100;

    .modal-inner {
        width: 600px;
    }

    .modal-content {
        z-index: 200;
        //height: calc(100vh - 150px);
        border: 1px solid @dv-c-accent-2;
        border-left-width: 6px;
        background: @dv-c-background;
        max-height: 600px;
        position: relative;
        overflow-y: auto;

        h1 {
            font-size: 20px;
            // text-align: center;
            margin-bottom: 8px;
        }

        .content {
            margin: 12px 0;
            height: 100%;

            p.legal {
                font-size: 12px;
            }
        }

        .got-it {
            text-align: center;
            position: fixed;
            bottom: 20px;
            left: 0;
            right: 0;
        }

        .page {
            display: none;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            padding: 16px 24px;
            &[active] {
                display: block;
            }
        }

        .page-indicator {
            margin-top: 20px;
            text-align: center;

            position: absolute;
            bottom: -30px;
            left: 0;
            right: 0;
            .padding-left(0px);

            .indicator {
                @diamond-size: 6px;
                @idle-color: @dv-c-accent-3;

                .variant(@color) {
                    border-bottom-color: @color;
                    &::after {
                        border-top-color: @color;
                    }
                }

                cursor: pointer;
                display: inline-block;
                margin: 2px 4px;
                width: 0;
                height: 0;
                border: @diamond-size solid transparent;
                border-bottom-color: @idle-color;
                position: relative;
                top: -@diamond-size;
                transition: border-color 0.125s ease-in;
                transform-origin: 50% 100%;

                &::after {
                    content: "";
                    position: absolute;
                    .left(-@diamond-size);
                    top: @diamond-size;
                    width: 0;
                    height: 0;
                    border: @diamond-size solid transparent;
                    border-top-color: @idle-color;
                    transition: border-color 0.125s ease-in;
                }

                &[active] {
                    .variant(@dv-c-foreground);
                }

                &[bad] {
                    .variant(@dv-c-red);
                }

                &:hover {
                    .variant(white);
                }

                &[disabled] {
                    pointer-events: none;
                    
                    border-bottom-color: @dv-c-disabled;
                    &::after {
                        border-top-color: @dv-c-disabled;
                    }
                }
            }
        }
    }
}

</style>

