<template>
    <div>
        <p v-if="event">条件：<b>{{event}}</b></p>
        <template  v-if="data.length">
            <strong>详情：</strong>
            <ul>
                <li v-for="(v,k) in data" :key="k">
                    <div v-if="v.map">地图: <strong>{{v.map}}</strong></div>
                    <div v-if="v.level">达成等级: <strong>Lv {{v.level}}</strong></div>
                    <div v-for="(vn,kn) in v.npc" :key="kn">
                        NPC: {{vn}}
                    </div>
                    <span v-if="v.favour">好感度值: {{v.favour}}</span>
                    <div v-if="v.enhanceLevel">需要强化 <strong>+{{v.enhanceLevel}}</strong> 装备</div>
                    <div v-if="v.eqRank">装备品质为 <strong>{{v.eqRank | rank}}</strong></div>
                    <div v-for="(vm,km) in v.monster" :key="km">
                        怪物：{{vm}}
                    </div>
                </li>
            </ul>
        </template>
    </div>
</template>
<script>
export default {
    name:'condition',
    props:["data","event"],
    filters:{
        rank(num){
            switch (num) {
                case 0:
                    return 'D级';
                case 1:
                    return 'C级';
                case 2:
                    return 'B级';
                case 3:
                    return 'A级';
                case 4:
                    return 'S级';
                case 5:
                    return '传说级';
                case 6:
                    return '命名级';
                case 7:
                    return '古代级';
                default:
                    break;
            }
        }
    }
}
</script>
<style lang="less" scoped>
ul{
    width: 500px;
    li{
        width: 250px;
		float: left;
        //list-style: none;
    }
}
</style>