<template>
    <div>
        <h1 class="divided">稀有商店</h1>
        <go-back />
         <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <div class="hidshoptable">
                <table border="2" class="labryinth-table">
                    <thead>
                        <th>商店</th>
                        <th>描述</th>
                        <th v-if="showGroupProb">组内概率</th>
                        <th style="width:310px">贩售商品</th>
                        <th>出现条件</th>
                    </thead>
                        <tr v-for="(v,k) in combinecell" :key="k" style="height:70px;">
                            <td :rowspan="v._ButtonImgIDspan" :class="{hidden: v._ButtonImgIDdis}"><img :src="npcPic(v._FirstNPCImg)"></td>
                            <td :rowspan="v._ButtonImgIDspan" :class="{hidden: v._ButtonImgIDdis}" v-html="v.desc"></td>
                            <template v-if="showGroupProb">
                                <td v-if="v.prob === 1">-</td>
                                <td v-else>{{v.prob | rate}}%</td>
                            </template>
                            <td>
                                <div class="Title">
                                    <div v-for="(vv,kk) in v.items" :key="kk" class="titleitem">
                                        <div class="hidshop-item">
                                            <item-icon-tooltip
                                                    :itemId="vv.itemId" 
                                                    :itemStub="vv.item"
                                                    :count="vv.count"
                                                    :noLink="noLink">
                                            </item-icon-tooltip>
                                            价格：{{vv.price}}
                                        </div>
                                    </div>
                                </div>
                            </td>
                            <td :rowspan="v._GroupIDspan" :class="{hidden: v._GroupIDdis}"><a href="javascript:void(0)" @click="checkCondition(v._GroupID)">查看</a></td>
                        </tr>
                </table>
            </div>
            <template v-if="popup && groupId" >
                <dialog-frame :title="title" :visible="popup" height="480px" @closePopup="closeDialog">
                    <div v-if="conditionLoading">
                        <load-indicator loadText="正在加载中"></load-indicator>
                    </div>
                    <div v-else>
                        <div v-if="conditionResult.info.limit">频率：{{conditionResult.info.limit}}</div>
                        <div v-else style="color:#3fd63f;font-weight:bold;">无限制</div>
                        <div style="margin-top:1rem;">概率：{{conditionResult.info._GroupProb}}%</div>
                        <condition :data="conditionResult.achieve" :event="conditionResult.info.event"></condition>
                    </div>
                </dialog-frame>
            </template>    
        </template>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import Loader from "@/components/util/Loader";
import DialogFrame from "@/components/util/DialogFrame";
import Condition from "@/components/condition/Condition"
Vue.component("load-indicator", Loader);
export default {
    created(){
        this.fetchData();
    },
    components: {
        GoBack,
        ItemIconTooltip,
        DialogFrame,
        Condition
    },
    data(){
        return{
            result:null,
            noLink:false,
            loading:true,
            popup:false,
            groupId:0,
            title:null,
            conditionResult:{
                info:{
                    limit:null
                },
                achieve:null,
            },
            conditionLoading:true,
            groupCache:[],
        }
    },
    methods:{
        fetchData(){
            if(GameInfo.HidshopCache){
                this.result = GameInfo.HidshopCache;
                this.loading = false;
            }else{
            GameInfo.HidShop(this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        },
        checkCondition(id){
            this.popup = true;
            this.title = '出现条件';
            this.groupId = parseInt(id);
            this.conditionLoading = true;
            console.log(this.groupCache)
            if(this.groupCache[id] ){
                this.conditionResult = this.groupCache[id];
                this.conditionLoading = false;
            }else{
                GameInfo.HidShopCondition(id,this.$store.state.regionCode,
                    (res) => {
                        this.groupCache[id] = res;
                        this.conditionResult = res;
                        this.conditionLoading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.conditionLoading = false;
                    });
            }
        },
        closeDialog(){
            this.popup = false;
        },
        npcPic(file){
            return `https://reference.dn.divinitor.com/api/server/ko/dds/${file}/png`;
        }
    },
    computed:{
        combinecell(){
            let list = this.result;
            for (let field in list[0]) {
                let k = 0;
                while (k < list.length) {
                    list[k][field + 'span'] = 1;
                    list[k][field + 'dis'] = false;
                    let i = k + 1;
                    for (i; i <= list.length - 1; i++) {
                        if (list[k][field] == list[i][field] && list[k][field] != '') {
                            list[k][field + 'span']++;
                            list[k][field + 'dis'] = false;
                            list[i][field + 'span'] = 1;
                            list[i][field + 'dis'] = true;
                        } else {
                            break;
                        }
                    }
                    k = i;
                }
            }
            return list;
        },
        showGroupProb(){
            for (let index = 0; index < this.result.length; index++) {
                if(this.result[index].prob != 1){
                    return true;
                }
            }
            return false;
        },
    }
}
</script>
<style lang="less" scoped>
@import "../../less/core.less";
.hidshoptable{
    overflow-x: auto;
    overflow-y:hidden;
    .labryinth-table{
        margin:0 auto;
        tr:hover {
            background: rgba(19, 19, 21, 0.4);
        }
        .hidden{
            display:none;
        }

        .Title {
            display: flex;
            flex-direction: row;

            .titleitem {
                flex: 0 0 auto;
                margin-right: 1rem;

                .hidshop-item{
                    display:flex; 
                    align-items:center
                }
            }
        }

    }
}
</style>