<template>
    <div>
        <h1 class="divided">分裂迷宫</h1>
        <go-back />
         <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <div>
                <h3>时间</h3>
                <h4>{{result.schedule.start}} ~ {{result.schedule.end}}</h4>
                <h3>预期奖励</h3>
                <div class="Title">
                    <div v-for="(vv,kk) in result.items" :key="kk" class="titleitem">
                        <item-icon-tooltip
                            :itemId="vv.id" 
                            :itemStub="vv"
                            :count="vv.count"
                            :noLink="noLink">
                        </item-icon-tooltip>
                    </div>
                </div>
                排名奖励
                <table class="labryinth-table">
                    <thead>
                        <th>排名</th>
                        <th>奖励</th>
                    </thead>
                        <tr v-for="(v,k) in result.rank" :key="k">
                            <td>{{v.grade}}</td>
                            <td>
                                <div class="Title">
                                    <div v-for="(vv,kk) in v.items" :key="kk" class="titleitem">
                                        <item-icon-tooltip
                                            :itemId="vv.item.id" 
                                            :itemStub="vv.item"
                                            :count="vv.count"
                                            :noLink="noLink">
                                        </item-icon-tooltip>
                                    </div>
                                </div>
                            </td>
                        </tr>
                </table>
            </div>    

        </template>
        <back-to-top class="topButton"><i class="fa fa-arrow-up"></i></back-to-top>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import Loader from "@/components/util/Loader";
import BackToTop from '@/components/util/BacktoTop';
Vue.component("load-indicator", Loader);
export default {
    created(){
        this.fetchData();
    },
    components: {
        GoBack,
        ItemIconTooltip,
        BackToTop
    },
    data(){
        return{
            result:null,
            noLink:false,
            loading:true,
        }
    },
    methods:{
        fetchData(){
            if(GameInfo.alteiaCache){
                this.result = GameInfo.alteiaCache;
                this.loading = false;
            }else{
            GameInfo.Alteia(this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import "../../less/core.less";
.Title {
    display: flex;
    flex-direction: row;

    .titleitem {
        flex: 0 0 auto;
    }
}

.labryinth-table{
    margin:0 auto;
}
</style>